// Styles for RSVP card.
@import "index";
@import "colors";

.rsvp {
  @extend .card;
}

.content {
  max-width: 800px;
  margin: 0 auto;
}

.title {
  margin-top: 0;
}

.saveTheDateNotice {
  margin: 0 auto;
}

.form {
  margin: 0px auto;

  .formHeader,
  .formFooter {
    margin: 16px;
  }

  .formError {
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .nameInput {
    margin: 0;
  }

  .submitButton {
    .submitIcon {
      margin-left: 8px;
      font-size: inherit;
    }
  }
}

.families {
  max-height: 400px;
  overflow-y: auto;
  border-top: 1px solid;
  border-bottom: 1px solid;
  border-color: $primary;

  @media #{$screen-tablet} {
    & {
      max-height: initial;
    }
  }

  .familyName {
    @extend .overline;
  }
}

.familyMembers {
  width: 100%;
}

.familyMember {
  display: flex;
  align-items: center;

  .familyMemberName {
    flex: 3 0 0px;
  }
}

.checkboxWithLabel {
  flex: 1 1 0px;
  padding: 0 8px;
  .selector {
    width: 100%;
    text-align: left;
  }
}

.familyMemberFieldsMobile {
  display: none;
  padding-bottom: 16px;
  // border-bottom: 1px solid $primary;
}

@media #{$screen-tablet} {
  .selector .selectorLabel {
    font-size: 0.8em;
  }
  .familyMemberFieldsMobile {
    display: flex;
    justify-content: center;
  }
  .familyMember .checkboxWithLabel {
    display: none;
  }
  .checkboxWithLabel {
    flex: 0 0 27%;
  }
  .checkboxWithLabel:empty {
    display: none;
  }
}
